@use '../abstracts/variables';

// float하는 상위 DOM에 선언
@mixin clearfix {
	&:after {
		content: '';
		display: block;
		width: 100%;
		height: 0;
		clear: both;
	}
}

// 한줄 말줄임표
@mixin ellipsis($autoWidth: true, $isInline: true, $clearLineHeight: true) {
	overflow: hidden;

	@if ($isInline) {
		display: inline-block;
	} @else {
		display: block;
	}

	@if ($autoWidth) {
		width: 100%;
	}
	max-width: 100%;
	@include clear_multi_ellipsis; // 멀티 라인 값 클리어
	text-overflow: ellipsis;
	white-space: nowrap;
}

// n줄 말줄임표
@mixin multi_ellipsis($line-height, $lines: 2, $useHeight: true) {
	display: block;
	display: -webkit-box;
	overflow: hidden;
	@if $useHeight {
		height: $line-height * $lines;
	}
	max-height: $line-height * $lines;
	line-height: $line-height;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: $lines;
	text-overflow: -o-ellipsis-lastline;
	white-space: normal;
}

// 멀티 라인 값 클리어
@mixin clear_multi_ellipsis {
	height: auto;
	max-height: none;
	-webkit-box-orient: inherit;
	-webkit-line-clamp: inherit;
}

// Display Flex를 활용한 영역대비 사이즈 자동 조절
// $colAlign : align-items, $rowAlign : justify-contents, $direction: fles-direction, $isInline : display
@mixin justify_width($colAlign: start, $rowAlign: start, $direction: row, $isInline: false) {
	@if ($isInline) {
		display: -ms-inline-flexbox;
		display: -webkit-inline-box;
		display: inline-flex;
		display: -webkit-inline-flex;
	} @else {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		display: -webkit-flex;
	}

	@if ($rowAlign == between) {
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	} @else if ($rowAlign == around) {
		justify-content: space-around;
		-webkit-justify-content: space-around;
	} @else if ($rowAlign == center) {
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
		-webkit-justify-content: center;
	} @else if ($rowAlign == start) {
		-webkit-box-pack: start;
		-ms-flex-pack: start;
		justify-content: flex-start;
	} @else if ($rowAlign == end) {
		-webkit-box-pack: end;
		-ms-flex-pack: end;
		justify-content: flex-end;
	}

	@if ($colAlign == center) {
		-webkit-box-align: center;
		-moz-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
	} @else if ($colAlign == start) {
		-webkit-box-align: start;
		-moz-box-align: start;
		-ms-flex-align: start;
		-webkit-align-items: flex-start;
		align-items: flex-start;
	} @else if ($colAlign == end) {
		-webkit-box-align: end;
		-moz-box-align: end;
		-ms-flex-align: end;
		-webkit-align-items: flex-end;
		align-items: flex-end;
	}

	@if ($direction == row) {
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		-moz-box-orient: horizontal;
		-moz-box-direction: normal;
		-ms-flex-direction: row;
		-webkit-flex-direction: row;
		flex-direction: row;
	} @else if ($direction == row-reverse) {
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		-moz-box-orient: horizontal;
		-moz-box-direction: normal;
		-ms-flex-direction: row-reverse;
		-webkit-flex-direction: row-reverse;
		flex-direction: row-reverse;
	} @else if ($direction == column) {
		-webkit-box-orient: vertical;
		-webkit-box-direction: reverse;
		-moz-box-orient: vertical;
		-moz-box-direction: reverse;
		-ms-flex-direction: column;
		-webkit-flex-direction: column;
		flex-direction: column;
	}
}

@mixin set_placeholder_color($color: rgba(255, 255, 255, .65)) {
	&::-webkit-input-placeholder {
		font-size: inherit;
		color: $color;
	}

	&:-ms-input-placeholder {
		font-size: inherit;
		color: $color;
	}

	&::-ms-input-placeholder {
		font-size: inherit;
		color: $color;
	}

	&::placeholder {
		font-size: inherit;
		color: $color;
	}

	&:-ms-input-placeholder {
		font-size: inherit;
		color: $color;
	}

	&::-ms-input-placeholder { /* Microsoft Edge */
		font-size: inherit;
		color: $color;
	}
}

@mixin no_select_text {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

@mixin transition($props...) {
	-webkit-transition: $props;
	-moz-transition: $props;
	-ms-transition: $props;
	-o-transition: $props;
	transition: $props;
}

@mixin transform($props...) {
	-webkit-transform: $props;
	-moz-transform: $props;
	-ms-transform: $props;
	-o-transform: $props;
	transform: $props;
}

